.box {
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  margin: 1rem;
  position: absolute;
  z-index: 1;
  background-color: white;
  left: -370%;
  display: none;
}
.navbar__menu:nth-child(3) .box {
 left: -520%;
}

.box__content {
  padding: 1rem;
}

.box__arrow-wrapper {
  left: calc(50% - 0.5rem);
  position: absolute;
  top: 0;
  z-index: 1;
  text-align: center;
}

.box__arrow {
  position: absolute;
  z-index: 2;
  top: -8px;
  display: inline-block;
  background: white;
  height: 1rem;
  width: 1rem;
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.75);
  transform: rotate(45deg);

  &:after {
    content: "";
    position: absolute;
    background: white;
    display: inline-block;
    height: 2rem;
    top: -0.3rem;
    width: 1rem;
    z-index: 2;
    transform: rotate(45deg);
  }

}