.navbar__logo {
  transform: scale(0.7);
  width: 80px;
  height: 63px;
	background-image: url(data:image/svg+xml;base64,);
}

.navbar {
  position: relative;
  display: flex;
  justify-content: space-between;
  // overflow: hidden;
}

.navbar__drawer {
  cursor: pointer;
  order: -1;
  font-size: 1.6rem;
  padding: 20px;
}

.navbar__action-list {

  display: flex;
}

.navbar__action {
  font-size: 1.6rem;
  padding: 20px 10px;

  &:not(:last-child) {
    display: none;
  }
}

.navbar__menu-list {
  position: absolute;
  font-size: 16px;
  top: 60px;
  display: flex;
  flex-direction: column;
  left: -100%;
  color: $color-san-juan;
  font-weight: bold;
  letter-spacing: 2px;
}

.navbar__menu {
  padding: 10px;
  text-transform: uppercase;
  color: $color-san-juan;
  position: relative;
}

#anything-you-want:checked + .navbar__menu-list {
  left: 0%;
  transition: 0.3s;
} 

#anything-you-want {
  display: none;
}