@media screen and (min-width: 768px) {
   .announce {
     position: relative;
   }

   .announce__bottom {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;

    //  make the container ignore mouse events
    // like you can click through it
    pointer-events: none;
   }

   .announce__button{
     background-color: transparent;
   }

   .announce__button-list {
    margin-left: 4rem;
   }

   .announce__drop-down {
    margin-right: 4rem;
   }
}