@font-face {
    font-family: 'wmw';
    src:
      url('asset/font/wmw.ttf?3q5tet') format('truetype'),
      url('asset/font/wmw.woff?3q5tet') format('woff'),
      url('asset/font/wmw.svg?3q5tet#wmw') format('svg');
    font-weight: normal;
    font-style: normal;
  }
  
  [class^="wmw-"], [class*=" wmw-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'wmw' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
